
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}

h1, h2 {
  font-weight: normal;
}
.chatty-cat {
  max-width: 150px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 50%;
  z-index: 1;
}
.hill{
  height: 2000px;
  width: 2000px;
  background-color: #55efc4;
  border-radius: 50%;
  display: inline-block;
  position: fixed;
  top: 500px;
  left: -800px;
  z-index: -1;
}
ul {
  list-style-type: none;
  padding: 0;
}
li {
  display: inline-block;
  margin: 0 10px;
}
a {
  color: #42b983;
}
.bubble {
  position: relative;
  background: #fff;
  border-radius: .4em;
  height: 600px;
  --speakfrom: -10px;
}
.bubble:after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 0;
  border: 20px solid transparent;
  border-top-color: #fff;
  margin-left: var(--speakfrom);
  border-bottom: 0;
  margin-bottom: -20px;
}
.inBubble{
  padding:50px;
}
.logout {
  position: fixed;
  top: 0;
  left: 80%;
  color: #FFF;
  text-decoration: none;
}





































































































img {
  width: 140px;
  height: 140px;
}
.caption {
  float: left;
  position: relative;
  bottom: 25px;
  right: 53px;
}
.cat{
  position: fixed;
  bottom:0;
  margin-bottom: -10px
}





























































input {
  border-style:solid;
}
.error {
  border: 3px solid red;
}

.error {
  border: 3px solid red;
}
















































































































#response{
  border-radius: 2px;
  width: 300px;
}
#submit{
  display: block;
  margin-left: auto;
  margin-right: auto;
  border: 2px;
  width: 300px;
  height: 40px;
  margin-top: 10px;
}

.rFriend {
  position: fixed;
top: 50px;
left: 250px;
color: red;
}


.error[data-v-2c4c9fa6] {
  border: 3px solid red;
}
